import React, { useEffect, useRef, useState } from 'react'
import { Link, Navigate } from 'react-router-dom';
import { useStateContext } from '../contexts/ContextProvider';
import axiosClient from '../axios-client';
import Loader from '../components/Loader';

export default function AddNewPost() {
    
    const [msg, setMsg] = useState()
    const [errors, setErrors] = useState(null)
    const [isLoading, setIsLoading] = useState(true)
    
    const titleRef = useRef()
    const titleImageRef = useRef()
    const categoryRef = useRef()
    const descriptionRef = useRef()
    const imageRef = useRef()
    const captionRef = useRef()
    const tagRef = useRef()
    const mapRef = useRef()

    const { token } = useStateContext()
    const [category, setCategory] = useState([])

    useEffect(() => {
        document.title = 'Add New Post - React Blog';
        axiosClient.get('/get-category')
            .then((res) => {
                setCategory(res.data)
                setTimeout(() => {
                    setIsLoading(false);
                  }, 500);
            })
    }, []);

    const onSubmit = (event) => {
        event.preventDefault();

        const formData = new FormData();
        formData.append("title", titleRef.current.value);
        formData.append("titleImage", titleImageRef.current.files[0]);
        formData.append("category", categoryRef.current.value);
        formData.append("description", descriptionRef.current.value);
        formData.append("image", imageRef.current.files[0]);
        formData.append("caption", captionRef.current.value);
        formData.append("tag", tagRef.current.value);
        formData.append("map", mapRef.current.value);

        axiosClient.post("/post-store", formData, {
            "headers": {
                "Accept": "application/json",
                "Content-Type": "multipart/form-data"
            }
        })
        .then(({data}) => {
            setMsg(data.message)
            setTimeout(()=> {
                setMsg(null)
            },10000)

            titleRef.current.value = "";
            titleImageRef.current.value = "";
            categoryRef.current.value = "";
            descriptionRef.current.value = "";
            imageRef.current.value = "";
            captionRef.current.value = "";
            tagRef.current.value = "";
            mapRef.current.value = "";
        })
        .catch(err => {
            const response = err.response;
            if (response && response.status === 422) {
                setErrors(response.data.errors)
            }
        })
    }
    if (!token) {
        return <Navigate to="/login"/>
    }
    
    return (
    <>
        {isLoading ? <Loader /> :
            <div className='max-w-screen-xl mx-auto px-4 mt-3'>
                {msg && 
                    <div className="p-4 mb-4 text-sm text-blue-800 rounded-lg bg-blue-50 dark:bg-gray-800 dark:text-blue-400" role="alert">
                        {msg}
                    </div>
                }

                {errors &&
                    <div className="p-4 mb-4 text-sm text-blue-800 rounded-lg bg-blue-50 dark:bg-gray-800 dark:text-blue-400" role="alert">
                        {Object.keys(errors).map(key => (
                            <p key={key}>{errors[key][0]}</p>
                        ))}
                    </div>
                }
                <div className='bg-gray-200 dark:bg-gray-700 min-h-[540px] rounded-xl mb-5'>
                    <div className='flex justify-between pt-3'>
                        <div className='text-2xl dark:text-white ml-5'>
                            Add New Post
                        </div>
                        <div className='mr-5 mt-2'>
                            <Link to='/posts' className='rounded-full text-white dark:text-black bg-black dark:bg-white px-4 py-2'>Posts</Link>
                        </div>
                    </div>
                    <div className='px-5 py-5'>
                        <div className='px-10 bg-gray-300 dark:bg-gray-900 p-5 rounded-xl'>                        
                            <form onSubmit={onSubmit}>                            
                                <div className='flex flex-row'>                                                                
                                    <div className="mb-6 basis-2/4 pr-2">
                                        <label htmlFor="title" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Title</label>
                                        <input type="text" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post Title" ref={titleRef} required />
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                    <label htmlFor="titleImage" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Title Image</label>
                                    <input type='file' id="titleImage" className="image-input bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={titleImageRef} required />
                                    </div>
                                </div>
                                <div className='flex flex-row'>                                                                
                                    <div className="mb-6 basis-2/4 pr-2">
                                        <label htmlFor="category" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Category</label>
                                        <select name="category" id="category" className="mt-1 bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={categoryRef} required >
                                                <option value="1">Select Category</option>
                                                {category &&
                                                    category.map((item) => {
                                                        return <option value={item.id} key={item.id}>{item.name}</option>;                                            
                                                    })
                                                }
                                        </select>
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                    <label htmlFor="tag" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Tag</label>
                                        <input type="text" id="tag" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post Tag" ref={tagRef} required />
                                    </div>
                                </div> 
                                <div className='flex flex-row'>                                                                
                                    <div className="mb-6 basis-2/4 pr-2">                                    
                                        <label htmlFor="image" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Post Image</label>
                                        <input type='file' id="image" className="image-input bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={imageRef} required />
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                    <label htmlFor="caption" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Image Caption</label>
                                        <input type="text" id="caption" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Image caption" ref={captionRef} required />
                                    </div>
                                </div>                                                                                            
                                <div className="mb-6">
                                    <label htmlFor="description" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Description</label>
                                    <textarea rows="10" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post Title" ref={descriptionRef} required> </textarea> 
                                </div>
                                <div className="mb-6">
                                    <label htmlFor="map" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Map</label>
                                    <input type="text" id="map" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Map Link (optional)" ref={mapRef} /> 
                                </div>
                                <div className='flex'>
                                    <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Submit</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        }
    </>
  )
}
