import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Loader from '../components/Loader';

export default function NotFound() {

  const [isLoading, setIsLoading] = useState(true)

  const [darkMode, setDarkMode] = useState('');

  const [darkModeSwitch] = useState(JSON.parse(localStorage.getItem('dmode')));

  useEffect(() => {
    document.title = '404 - Page not found';
    setTimeout(() => {
      setIsLoading(false);
    }, 500);
  }, []);

  useEffect(() => {
    if (darkModeSwitch) {
      setDarkMode('dark');
      localStorage.setItem('dmode', true);
    }
    else {
      setDarkMode('');
      localStorage.setItem('dmode', false);
    }
  }, [darkModeSwitch])

  return (
    <div className={`${darkMode}`}>
        <div className='h-screen dark:bg-gray-900'>
        {isLoading ? <Loader /> : 
          <div className='max-w-screen-xl mx-auto pt-[80px]'>
              <div className='bg-gray-200 dark:bg-gray-700 h-[540px] rounded-xl'>
                <div className='flex justify-center'>
                  <img src="/img/404.png" alt="Page not found" />
                </div>
                <div className='flex justify-center'>
                  <Link to='/' className='text-center bg-gray-800 px-5 py-2 rounded text-white'>GO HOME</Link>
                </div>
              </div>
          </div>
        }
      </div>
    </div>
  )
}