import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import axiosClient from '../axios-client';
import Loader from '../components/Loader';

export default function Search() {
  
    const params = useParams()
    const [searchData, setSearchData] = useState([])
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        
        axiosClient.post('/search', {search:params.query})
            .then(({data}) => {
                setSearchData(data)
                setTimeout(() => {
                    setIsLoading(false);
                  }, 500);
            })
    }, [params])
    
    return (
        <>
            {isLoading ? <Loader /> :
                <div className='max-w-screen-xl mx-auto px-4 mb-5'>
                    <div className="bg-gray-100 dark:bg-gray-500 rounded-2xl min-h-[580px]">                        
                        <div className="text-center text-black dark:text-white bg-gray-200 dark:bg-gray-700 py-4 rounded-t-2xl text-lg">
                            <b>Search key:</b> <i>{params.query }</i>
                        </div>
                        <div className='p-5'>
                            {searchData.length ? 
                                searchData.map((item, index) => {
                                    return(
                                        <div className="flex flex-row mb-5" key={index}>
                                            <div className='flex-none w-64 h-64'>
                                                <Link to={`/post/${item.slug}`}>
                                                    <img className='h-64 w-64' src={item.title_image} alt="Post"/>   
                                                </Link>
                                            </div>
                                            <div className='h-64 p-5 bg-gray-200 dark:bg-gray-700 text-black dark:text-white grow'>
                                                <Link to={`/post/${item.slug}`}><h1 className='text-xl mt-2 mb-5'>{item.title.substring(0, 65)}</h1></Link>
                                                <p className='mb-5'>{item.description.substring(0, 330)}</p>
                                                <Link to={`/post/${item.slug}`} className='bg-black text-white dark:bg-gray-200 dark:text-black p-2'>Details</Link>
                                            </div>
                                        </div>
                                    )
                                }):<div className='text-center text-3xl mt-5'>
                                    *No post in this search keyword..
                                </div>
                            }
                        </div>                        
                    </div>
                </div>
            }
        </>
    )
}
