import { Link, Outlet, useNavigate } from 'react-router-dom';
import { useStateContext } from '../contexts/ContextProvider';
import { useEffect, useState } from 'react';
import axiosClient from '../axios-client';
import Footer from '../pages/Footer';

export default function DefaultLayout() {
    
    const { user, token, setUser, setToken } = useStateContext();

    const [darkMode, setDarkMode] = useState('');

    const [darkModeSwitch, setDarkModeSwitch] = useState(JSON.parse(localStorage.getItem('dmode')));

    const navigate = useNavigate();
    
    const onLogout = (e) => {
        e.preventDefault()

        axiosClient.post('/logout')
        .then(() => {
            setUser({})
            setToken(null)
            navigate('/')
        })
    }

    useEffect(() => {
      if (darkModeSwitch) {
        setDarkMode('dark');
        localStorage.setItem('dmode', true);
      }
      else {
        setDarkMode('');
        localStorage.setItem('dmode', false);
      }
    }, [darkModeSwitch])
    

    useEffect(() => {
        if (token) {
            axiosClient.get('/user')
            .then(({data}) => {
                setUser(data)
            }) 
        }        
    }, [token, setUser])
    
    return (
    <div className={`${darkMode}`}>
        <div className='bg-white dark:bg-gray-900 dark:mode min-h-[100vh]'>                       
            <nav className="bg-white dark:bg-gray-900 w-full z-20 top-0 left-0 border-gray-200">
                    <div className="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4">
                    <Link href="/" className="flex items-center">
                        <img src="/img/logo.png" className="h-8 mr-3 rounded-lg" alt="Blog Logo" />
                        <span className="self-center text-2xl font-semibold whitespace-nowrap dark:text-white">Blog</span>
                    </Link>
                    <div className="flex md:order-2">                     
                        <div className='mt-1 mr-5'>
                            { darkModeSwitch ? (                           
                            <button type="button" onClick={() => {setDarkModeSwitch(!darkModeSwitch)}} title="Light Mode">
                                <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" viewBox="0 0 24 24" strokeWidth={1.5} stroke="#fff" className="w-6 h-6"><path strokeLinecap="round" strokeLinejoin="round" d="M12 3v2.25m6.364.386l-1.591 1.591M21 12h-2.25m-.386 6.364l-1.591-1.591M12 18.75V21m-4.773-4.227l-1.591 1.591M5.25 12H3m4.227-4.773L5.636 5.636M15.75 12a3.75 3.75 0 11-7.5 0 3.75 3.75 0 017.5 0z" /></svg>
                            </button>
                            ) : ( 

                            <button type="button" onClick={() => {setDarkModeSwitch(!darkModeSwitch)}} title="Dark Mode">
                                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6"><path strokeLinecap="round" strokeLinejoin="round" d="M21.752 15.002A9.718 9.718 0 0118 15.75c-5.385 0-9.75-4.365-9.75-9.75 0-1.33.266-2.597.748-3.752A9.753 9.753 0 003 11.25C3 16.635 7.365 21 12.75 21a9.753 9.753 0 009.002-5.998z" /></svg>
                            </button>
                            )}
                        </div>              

                        <div className='relative dropdownView'>
                            { token? <>
                            
                            <button type="button" id="dropdownDefaultButton" data-dropdown-toggle="dropdown" className="text-white bg-gray-700 hover:bg-blue-800 focus:outline-none font-medium rounded-lg text-sm px-4 py-2 text-center mr-3 md:mr-0 dark:bg-gray-800 dark:hover:bg-blue-700">
                                {user.name}
                            </button>

                            <div id="dropdown" className="hidden z-10 dropdownBody bg-gray-200 divide-y divide-gray-100 rounded-lg shadow w-[140px] dark:bg-gray-700 absolute top-[37px] right-0" >
                                <ul className="text-sm text-gray-700 dark:text-gray-200" aria-labelledby="dropdownDefaultButton" >
                                    <li>
                                        <Link to="/profile" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white rounded-t-lg">
                                            Profile
                                        </Link>
                                    </li>
                                    <li>
                                        <Link to="/posts" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">
                                            Posts
                                        </Link>
                                    </li>

                                    { user.role === 1 && 
                                    <li>
                                        <Link to="/admin" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">
                                            Admin Panel
                                        </Link>
                                    </li> }

                                    <li>
                                        <Link to="#" onClick={onLogout} method="post" as="button" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white text-left rounded-b-lg" style={{ width: "100%" }}>
                                            Logout
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                            
                            </>  : 

                            <Link to="/login" className="text-white bg-gray-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-4 py-2 text-center mr-3 md:mr-0 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Login</Link>

                            }
                            
                            <button data-collapse-toggle="navbar-sticky" type="button" className="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600" aria-controls="navbar-sticky" aria-expanded="false">
                                <span className="sr-only">Open main menu</span>
                                <svg className="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
                                    <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M1 1h15M1 7h15M1 13h15"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                    <div className="items-center justify-between hidden w-full md:flex md:w-auto md:order-1" id="navbar-sticky">
                        <ul className="flex flex-col p-4 md:p-0 mt-4 font-medium border border-gray-100 rounded-lg bg-gray-50 md:flex-row md:space-x-8 md:mt-0 md:border-0 md:bg-white dark:bg-gray-800 md:dark:bg-gray-900 dark:border-gray-700">
                        <li>
                            <Link to="/" className="block py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-blue-700 md:p-0 md:dark:hover:text-blue-500 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent dark:border-gray-700">Home</Link>
                        </li>
                        <li>
                            <Link to="/blog" className="block py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-blue-700 md:p-0 md:dark:hover:text-blue-500 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent dark:border-gray-700">Blog</Link>
                        </li>
                        <li>
                            <Link to="/about" className="block py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-blue-700 md:p-0 md:dark:hover:text-blue-500 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent dark:border-gray-700">About</Link>
                        </li>
                        <li>
                            <Link to="/services" className="block py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-blue-700 md:p-0 md:dark:hover:text-blue-500 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent dark:border-gray-700">Services</Link>
                        </li>
                        <li>
                            <Link to="/contact" className="block py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-blue-700 md:p-0 md:dark:hover:text-blue-500 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent dark:border-gray-700">Contact</Link>
                        </li>
                        </ul>
                    </div>
                    </div>
                </nav>
            <main>
                <Outlet />
            </main>
            <Footer />
        </div>
    </div>
    )
}
