import React, { useEffect, useState } from 'react'
import axiosClient from '../../axios-client';
import { Link } from 'react-router-dom';

export default function AdminCategory() {
    const [category, setCategory] = useState([]);
  
    useEffect(() => {
      document.title = 'Category - React Blog';
      axiosClient.get('/admin/category')
          .then((res) => {
            setCategory(res.data)
          })
    }, []);
  
    return (
      <>
        <div className='flex justify-between text-center text-3xl bg-gray-200 rounded p-2'>
          <div className='ml-3'>Categories</div>
          <div className='mr-3 text-lg mt-1'>
            <Link to='/admin/add-new-category' className='bg-gray-800 text-white px-3 py-2 rounded-lg'>Add New Category</Link>
          </div>
        </div>
  
        <div className='rounded mt-3'>        
          <div className="relative overflow-x-auto shadow-md sm:rounded-lg">
              <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400">
                  <thead className="text-l text-gray-700 uppercase dark:text-gray-400 border-b-2">
                      <tr>
                        <th scope="col" className="px-6 py-3 w-1/12">
                            No.
                        </th>
                        <th scope="col" className="px-6 py-3 w-2/12">
                            Name
                        </th>
                        <th scope="col" className="px-6 py-3 w-2/12">
                            Slug
                        </th>
                        <th scope="col" className="px-6 py-3 w-3/12">
                            Deatils
                        </th>
                        <th scope="col" className="px-6 py-3 w-1/12">
                            Count
                        </th>
                        <th scope="col" className="px-6 py-3 w-1/12">
                            Color
                        </th>
                        <th scope="col" className="px-6 py-3 text-center w-1/12">
                            Actions
                        </th>
                      </tr>
                  </thead>
                  <tbody>
                      {category.length ?
                        category.map((item,index) => {
                          return(
                            <tr className="border-b border-gray-200 dark:border-gray-700" key={index}>
                              <th scope="row" className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap bg-gray-50 dark:text-white dark:bg-gray-800">
                                {index+1}
                              </th>
                              <td className="px-6 py-4">
                                {item.name}
                              </td>
                              <td className="px-6 py-4 bg-gray-50 dark:bg-gray-800">
                                {item.slug}
                              </td>
                              <td className="px-6 py-4 bg-gray-50 dark:bg-gray-800">
                                {item.details}
                              </td>
                              <td className="px-6 py-4 bg-gray-50 dark:bg-gray-800">
                                <span className='inline-flex items-center justify-center w-3 h-3 p-3 ml-3 text-sm font-medium text-blue-800 bg-blue-100 rounded-full dark:bg-blue-900 dark:text-blue-300'>{item.posts_count}</span>
                              </td>
                              <td className="px-6 py-4 bg-gray-50 dark:bg-gray-800">
                                <div className={`h-5 w-20 ${item.category_color} text-center`}>
                                  
                                </div>
                              </td>                              
                              <td className="px-6 py-4">
                                <Link to={`/admin/category/${item.slug}`} className='rounded-full text-white dark:text-black bg-black dark:bg-white px-4 py-1 mr-1'>Posts</Link>
  
                                <Link to={`/admin/edit-post/${item.id}`} className='rounded-full text-white bg-yellow-500 px-4 py-1 mr-1'>Edit</Link>
                              </td>
                            </tr>
                          )
                        }) : <tr className="border-b border-gray-200 dark:border-gray-700" >
                          <td colSpan="6" className="px-6 py-4 text-center">
                            No category..!
                          </td>
                        </tr>
                      }            
                  </tbody>
              </table>
          </div>
        </div>
      </>
    )
}
