import React, { useEffect, useRef, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import axiosClient from '../../axios-client';

export default function AdminEditPost() {
    
    const params = useParams();
    const [post, setPost] = useState();

    const [msg, setMsg] = useState()
    const [errors, setErrors] = useState(null)
    const [category, setCategory] = useState([])
    
    const idRef = useRef()
    const titleRef = useRef()
    const titleImageRef = useRef()
    const categoryRef = useRef()
    const descriptionRef = useRef()
    const imageRef = useRef()
    const captionRef = useRef()
    const tagRef = useRef()
    const mapRef = useRef()

    useEffect(() => {
        axiosClient.get('/post/'+params.slug)
            .then((res) => {
                setPost(res.data)
            })
    }, [params]);

    useEffect(() => {
        axiosClient.get('/get-category')
            .then((res) => {
                setCategory(res.data)
            })
    }, []);

    const onSubmit = (e) => {
        e.preventDefault()

        const formData = new FormData();
        formData.append("id", idRef.current.value);
        formData.append("title", titleRef.current.value);
        formData.append("titleImage", titleImageRef.current.files[0]);
        formData.append("category", categoryRef.current.value);
        formData.append("description", descriptionRef.current.value);
        formData.append("image", imageRef.current.files[0]);
        formData.append("caption", captionRef.current.value);
        formData.append("tag", tagRef.current.value);
        formData.append("map", mapRef.current.value);

        axiosClient.post('/admin/post-edit', formData, {
            "headers": {
                "Accept": "application/json",
                "Content-Type": "multipart/form-data"
            }
        })
        .then(({data}) => {
            setPost(data.post)
            setMsg(data.message)
            setTimeout(()=> {
                setMsg(null)
            },5000)
        })
        .catch(err => {
            const response = err.response;
            if (response && response.status === 422) {
                setErrors(response.data.errors)
            }
        })
    }
  
    return (
        <>
            <div className='flex justify-between text-center text-3xl bg-gray-200 rounded p-2'>
                <div className='ml-3'>Edit Post</div>
                <div className='mr-3 text-lg mt-1'>
                    <Link to='/admin/posts' className='bg-gray-800 text-white px-3 py-2 rounded-lg'>Posts</Link>
                </div>
            </div>
            <div className='pt-3'>
                <div className='px-10 bg-gray-100 shadow-md dark:bg-gray-900 p-5 rounded'>                        
                    {post && (
                        <form onSubmit={onSubmit}> 
                        {errors &&
                                <div className="alert mb-3">
                                {Object.keys(errors).map(key => (
                                    <p key={key}>{errors[key][0]}</p>
                                ))}
                                </div>
                            }                           
                            <div className='flex flex-row'>                                                                
                                <div className="mb-6 basis-2/4 pr-2">
                                    <input type="hidden" name="id" defaultValue={post.id} ref={idRef} />
                                    
                                    <label htmlFor="title" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Title</label>
                                    <input type="text" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post Title" defaultValue={post.title} ref={titleRef} required />
                                </div>
                                <div className="mb-6 basis-2/4 pl-2">
                                <label htmlFor="titleImage" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Title Image</label>
                                <div className='flex justify-between'>
                                    <input type='file' id="titleImage" className="image-input bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={titleImageRef} />
                                    <img src={post.title_image} alt="postImage" className='h-[40px] w-[40px] ml-5'/>
                                </div>
                                </div>
                            </div>
                            <div className='flex flex-row'>                                                                
                                <div className="mb-6 basis-2/4 pr-2">
                                    <label htmlFor="category" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Category</label>
                                    <select name="category" id="category" className="mt-1 bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" defaultValue={post.category_id} ref={categoryRef} required >
                                            {category &&
                                                category.map((item) => {
                                                    return (
                                                    <option value={item.id} key={item.id} selected={post.category_id === item.id }>{item.name}</option>
                                                    );                                            
                                                })
                                            }
                                    </select>
                                </div>
                                <div className="mb-6 basis-2/4 pl-2">
                                <label htmlFor="tag" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Tag</label>
                                    <input type="text" id="tag" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post Tag" defaultValue={post.tag} ref={tagRef} required />
                                </div>
                            </div> 
                            <div className='flex flex-row'>                                                                
                                <div className="mb-6 basis-2/4 pr-2">                                    
                                    <label htmlFor="image" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Post Image</label>
                                    <div className='flex justify-between'>                                        
                                        <input type='file' id="image" className="image-input bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={imageRef} />
                                        <img src={post.image} alt="postImage" className='h-[40px] w-[100px] ml-5'/>
                                    </div>
                                </div>
                                <div className="mb-6 basis-2/4 pl-2">
                                <label htmlFor="caption" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Image Caption</label>
                                    <input type="text" id="caption" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Image caption" defaultValue={post.caption} ref={captionRef} required />
                                </div>
                            </div>                                                                                            
                            <div className="mb-6">
                                <label htmlFor="description" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Description</label>
                                <textarea rows="10" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Post description" ref={descriptionRef} required>{post.description}</textarea>
                            </div>
                            <div className="mb-6">
                                <label htmlFor="map" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Map</label>
                                <input type="text" id="map" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Map Link" defaultValue={post.map} ref={mapRef} required /> 
                            </div>
                            <div className='flex'>
                                <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Update</button>
                                <p className='pt-2 pl-5 text-black'>{msg}</p>
                            </div>
                        </form> 
                    )}
                </div>
            </div>
        </>
    )
}
