import { Navigate, createBrowserRouter } from "react-router-dom";
import DefaultLayout from "./components/DefaultLayout";
import Home from "./pages/Home";
import Blog from "./pages/Blog";
import SinglePost from "./pages/SinglePost";
import About from "./pages/About";
import Contact from "./pages/Contact";
import Services from "./pages/Services";
import NotFound from "./pages/NotFound";
import GuestLayout from "./components/GuestLayout";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Profile from "./pages/Profile";
import Posts from "./pages/Posts";
import AddNewPost from "./pages/AddNewPost";
import AdminLayout from "./components/AdminLayout";
import AdminDashboard from "./pages/admin/AdminDashboard";
import AdminPosts from "./pages/admin/AdminPosts";
import AdminComments from "./pages/admin/AdminComments";
import AdminUsers from "./pages/admin/AdminUsers";
import AdminSliders from "./pages/admin/AdminSliders";
import AdminAddNewPost from "./pages/admin/AdminAddNewPost";
import AdminEditPost from "./pages/admin/AdminEditPost";
import AdminCategory from "./pages/admin/AdminCategory";
import AdminAddNewCategory from "./pages/admin/AdminAddNewCategory";
import SingleCategory from "./pages/SingleCategory";
import AdminSingleCategory from "./pages/admin/AdminSingleCategory";
import PostEdit from "./pages/PostEdit";
import AdminContacts from "./pages/admin/AdminContacts";
import SingleTag from "./pages/SingleTag";
import Search from "./pages/Search";

const router = createBrowserRouter([
    {
        path: '/',
        element: <DefaultLayout />,
        children: [
            { path: '/', element: <Home />},
            { path: '/blog', element: <Blog /> },
            { path: '/posts', element: <Posts /> },
            { path: '/post/:slug', element: <SinglePost /> },
            { path: '/add-new-post', element: <AddNewPost /> },
            { path: '/edit-post/:slug', element: <PostEdit /> },
            { path: '/category/:slug', element: <SingleCategory /> },
            { path: '/tag/:tag', element: <SingleTag /> },
            { path: '/profile', element: <Profile /> },
            { path: '/about', element: <About /> },
            { path: '/services', element: <Services /> },
            { path: '/contact', element: <Contact /> },
            { path: '/search/:query', element: <Search /> },
        ] 
    },

    {
        path: '/',
        element: <GuestLayout />,
        children: [
            { path: '/login', element: <Login /> },
            { path: '/register', element: <Register /> }
        ]
    },

    {
        path: '/admin',
        element: <AdminLayout />,
        children: [
            { path: '/admin', element: <Navigate to="/admin/dashboard" /> },
            { path: '/admin/dashboard', element: <AdminDashboard /> },
            { path: '/admin/posts', element: <AdminPosts /> },
            { path: '/admin/comments', element: <AdminComments /> },
            { path: '/admin/users', element: <AdminUsers /> },
            { path: '/admin/sliders', element: <AdminSliders /> },
            { path: '/admin/add-new-post', element: <AdminAddNewPost /> },
            { path: '/admin/edit-post/:slug', element: <AdminEditPost /> },
            { path: '/admin/category', element: <AdminCategory /> },
            { path: '/admin/add-new-category', element: <AdminAddNewCategory/>},
            { path: '/admin/category/:slug', element: <AdminSingleCategory/>},
            { path: '/admin/contacts', element: <AdminContacts/>}
            
        ]
    },

    {
        path: '*', element: <NotFound />
    }
]);

export default router;