import { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import axiosClient from "../axios-client";
import { useStateContext } from "../contexts/ContextProvider";
import Loader from "../components/Loader";

export default function SinglePost()
{
    const params = useParams();
    const { token } = useStateContext(); 
    const [isLoading, setIsLoading] = useState(true);

    const [post, setPost] = useState();

    const [msg, setMsg] = useState();
    const commentRef = useRef();
    const postIdRef = useRef([]);

    useEffect(() => {
        axiosClient.get('/post/'+params.slug)
            .then((res) => {
                setPost(res.data)
                setTimeout(() => {
                    setIsLoading(false);
                  }, 500);
            })
    }, [params, msg]);

    document.title = post?.title+'- React Blog';

    const onSubmit = (e) => {
        e.preventDefault()
        const payload = {
            post_id: postIdRef.current.value,
            comment: commentRef.current.value
        }
        axiosClient.post('/comment', payload)
        .then(({data}) => {
            commentRef.current.value = "";
            postIdRef.current.value = "";
            setMsg(data.message)
            setTimeout(()=> {
                setMsg(null)
            },3000)
        })
    }
    
    return(
        <>
            {isLoading ? <Loader /> : <>
                {post &&
                    (<div className='max-w-screen-xl mx-auto px-4 mb-5'>
                    <div className="bg-gray-100 dark:bg-gray-500 rounded-2xl">
                        <div className="text-center text-black dark:text-white bg-gray-200 dark:bg-gray-700 py-4 rounded-t-2xl text-lg">
                            {post.title}
                        </div>
                        <div className="mt-7 text-center">
                            <img src={post.image} alt={post.title} className="w-[900px] h-[400px] block m-auto"/>
                            <p className="dark:text-white"><i><strong>Caption: </strong>{post.caption}</i></p>
                        </div>
                        <div className="px-20 py-7 text-black dark:text-white">
                            {post.description}
                        </div>
                        {post.map &&
                            (
                                <div className="mb-5">
                                    <div className="flex justify-center">
                                        <iframe title={post.title} src={post.map} className="h-[300px] w-[700px] rounded-lg"></iframe>
                                    </div>
                                </div>
                            )
                        }
                        <div className="px-20 py-3 text-black dark:text-white">
                            Category: {post.categories.name} <br />
                            Author: {post.users.name} <br />
                            Post date: {post.created_at} 
                        </div>
                        <div className="px-20 py-3 text-black dark:text-white">
                            <div className='flex flex-row'>
                                <div className='basis-5/12 border-t border-black dark:border-white w-full mt-4'></div>
                                <div className='basis-2/12 w-5/12 text-center text-lg'>
                                    Comments
                                </div>
                                <div className='basis-5/12 border-t border-black dark:border-white w-full mt-4'></div>
                            </div> 
                        </div>
                        <div className="px-20 text-black dark:text-white pb-5">
                            <p className="text-lg pb-5">
                                Comments
                            </p>
                            {token && 
                            <div className="w-5/12 mb-10">
                                <form onSubmit={(e)=> onSubmit(e)}>                                
                                    <div className="mb-3">
                                        <input type="hidden" name="id" ref={postIdRef} defaultValue={post.id}/>                                    
                                        <textarea id="message" rows="4" className="block w-full p-2.5 text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Leave a comment..." ref={commentRef} required></textarea>
                                    </div>

                                    <div className="flex">
                                        <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-4 py-1.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Comment</button>
                                        <p className='pt-1 ml-2 text-white'>{msg}</p>
                                    </div>
                                </form>
                            </div> }

                            {post.comments.length ? 
                            post.comments?.map((comment,index) => (
                                <div className="my-5" key={index}>
                                {/* Comments Message Start */}
                                    <div className="flex">
                                        <img className="h-10 w-10 rounded-full border border-gray-300 mt-3" src="/img/user.jpg" alt="User" />
                                        <div className="ml-3 rounded-lg border border-gray-300 p-2 w-5/12">
                                            <p className="dark:text-white">{comment.user.name}</p>
                                            <p className="text-gray-500 dark:text-gray-300">{comment.comment}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        )
                            : <p className="text-sm">
                            *No comments in this post...
                        </p>
                        } 
                        </div>
                    </div>
                </div>)

                }
            </>}
        </>
    )
}