import React, { useRef, useState } from 'react'
import { Link } from 'react-router-dom';
import axiosClient from '../../axios-client';

export default function AdminAddNewCategory() {

    const [msg, setMsg] = useState()
    const [errors, setErrors] = useState(null)

    const nameRef = useRef()
    const colorRef = useRef()
    const detailsRef = useRef()


    const onSubmit = (e) => {
        e.preventDefault()
        const payload = {
            name: nameRef.current.value,
            color: colorRef.current.value,
            details: detailsRef.current.value
        }
        axiosClient.post('/admin/add-new-category', payload)
        .then(({data}) => {
            setMsg(data.message)
            setTimeout(()=> {
                setMsg(null)
            },3000)

            nameRef.current.value = "";
            colorRef.current.value = "";
            detailsRef.current.value = "";
        })
        .catch(err => {
            const response = err.response;
            if (response && response.status === 422) {
                setErrors(response.data.errors)
            }
        })
    }

    return (
        <>
          <div className='flex justify-between text-center text-3xl bg-gray-200 rounded p-2'>
            <div className='ml-3'>Add New Category</div>
            <div className='mr-3 text-lg mt-1'>
              <Link to='/admin/category' className='bg-gray-800 text-white px-3 py-2 rounded-lg'>Categories</Link>
            </div>
          </div>
            {msg && 
                <div className="p-4 mt-2 text-sm text-blue-800 rounded-lg bg-blue-50 dark:bg-gray-800 dark:text-blue-400" role="alert">
                    {msg}
                </div>
            }
          <div className='pt-3'>
            <div className='px-10 bg-gray-100 shadow-md dark:bg-gray-900 p-5 rounded-xl'>                        
                <form onSubmit={onSubmit}> 
                {errors &&
                        <div className="alert mb-3">
                        {Object.keys(errors).map(key => (
                            <p key={key}>{errors[key][0]}</p>
                        ))}
                        </div>
                    }                           
                    <div className='flex flex-row'>                                                                
                        <div className="mb-6 basis-2/4 pr-2">
                            <label htmlFor="name" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Name</label>
                            <input type="text" id="name" name='name' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Category Name" required ref={nameRef}/>
                        </div>
                        <div className="mb-6 basis-2/4 pl-2">
                            <label htmlFor="category_color" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Color</label>
                            
                            <select className='g-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500' ref={colorRef} required>
                                <option value="bg-red-400">Default</option>
                                <option value="bg-orange-400">Orange</option>
                                <option value="bg-green-400">Green</option>
                                <option value="bg-sky-400">Sky</option>
                                <option value="bg-purple-400">Purple</option>
                            </select>
                        </div>
                    </div>                                                                                           
                    <div className="mb-6">
                        <label htmlFor="details" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Details</label>
                        <input type='text' id="details" name='details' className="image-input bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 h-[42px]" required ref={detailsRef}/>
                    </div>
                    <div className='flex'>
                        <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Submit</button>
                    </div>
                </form> 
            </div>
          </div>
        </>
      )
}
