import { useEffect, useRef, useState } from 'react'
import { useStateContext } from '../contexts/ContextProvider';
import axiosClient from '../axios-client';
import { Navigate } from 'react-router-dom';
import Loader from '../components/Loader';

export default function Profile() {
    
    const idRef = useRef()
    const nameRef = useRef()
    const emailRef = useRef()
    const passwordRef = useRef()
    const newPasswordRef = useRef()
    const passwordConfirmationRef = useRef()
    const [errors, setErrors] = useState(null)
    const [msg, setMsg] = useState()    
    const { user, setUser, token } = useStateContext()
    const [isLoading, setIsLoading] = useState(true)

    useEffect(() => {
        document.title = 'Profile - React Blog';
        setTimeout(() => {
            setIsLoading(false);
          }, 500);
    }, []);

    if (!token) {
        return <Navigate to="/login"/>
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const payload = {
            id: idRef.current.value,
            name: nameRef.current.value,
            email: emailRef.current.value,
            password: passwordRef.current.value,
            password_new: newPasswordRef.current.value,
            password_confirmation: passwordConfirmationRef.current.value,
        }
        axiosClient.post('/user-profile', payload)
        .then(({data}) => {
            setUser(data.user)
            setMsg(data.message)
            setTimeout(()=> {
                setMsg(null)
            },3000)
        })
        .catch(err => {
            const response = err.response;
            if (response && response.status === 422) {
                setErrors(response.data.errors)
            }
        })
    }

    return (
    <>
        {isLoading ? <Loader /> :
            <div className='max-w-screen-xl mx-auto px-4 mt-3'>
                <div className='bg-gray-200 dark:bg-gray-700 min-h-[540px] rounded-xl'>
                    <div className='text-center text-2xl dark:text-white pt-3'>
                        Profile
                    </div>
                    <div className='min-h-[400px]'>
                        <div className='flex justify-center mt-4'>
                            <img src="/img/user.jpg" alt="user" className='h-28 rounded-full border' />
                        </div>
                        <div className='px-10 mt-5'>                        
                            <form onSubmit={onSubmit}>
                                {errors &&
                                    <div className="alert mb-3">
                                    {Object.keys(errors).map(key => (
                                        <p key={key}>{errors[key][0]}</p>
                                    ))}
                                    </div>
                                }
                                <div className='flex flex-row'>                                                                
                                    <div className="mb-6 basis-2/4 pr-2">
                                        <input type="hidden" name="id" defaultValue={user.id} ref={idRef} />
                                        <label htmlFor="name" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Name</label>
                                        <input type="text" id="name" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Jone Due" defaultValue={user.name} ref={nameRef} required />
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                    <label htmlFor="email" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Email</label>
                                        <input type="email" id="email" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="jone@example.com" defaultValue={user.email} ref={emailRef} required />
                                    </div>
                                </div>
                                <div className='flex flex-row'>
                                    <div className="mb-6 basis-2/4 pr-2">
                                        <label htmlFor="role" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Role</label>
                                        <input type="text" id="role" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="User role" defaultValue={user.role === 0 ? 'User' : 'Admin'} disabled />
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                        <label htmlFor="password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Current password</label>
                                        <input type="password" id="password" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={passwordRef} placeholder='••••••••' required />
                                    </div>
                                </div>
                                <div className='flex flex-row'>
                                    <div className="mb-6 basis-2/4 pr-2">
                                    <label htmlFor="new_password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">New password</label>
                                        <input type="password" id="new_password" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={newPasswordRef} placeholder='••••••••' required />
                                    </div>
                                    <div className="mb-6 basis-2/4 pl-2">
                                        <label htmlFor="confirm_password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Confirm password</label>
                                        <input type="password" id="confirm_password" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ref={passwordConfirmationRef} placeholder='••••••••' required />
                                    </div>
                                </div>
                                <div className='flex pb-5 mb-5'>
                                    <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Update</button>
                                    <p className='pt-2 pl-5 text-white'>{msg}</p>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        }
    </>
  )
}
