import { useEffect, useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axiosClient from "../axios-client";
import BlogPostPaginate from "../components/BlogPostPaginate";
import Loader from "../components/Loader";

export default function Blog()
{
    const [post, setPost] = useState()
    const [currentItems, setCurrentItems] = useState([])
    const [category, setCategory] = useState()
    const [topCategories, setTopCategories] = useState()
    const [popularTags, setPopularTags] = useState()
    const [featurePost, setFeaturePost] = useState()
    const searchRef = useRef()
    const [isLoading, setIsLoading] = useState(true);

    const navigate = useNavigate()
   
    useEffect(() => {
        document.title = 'Blog - React Blog';
        axiosClient.get('/blog-post')
            .then((res) => {
                setPost(res.data)
                setIsLoading(false)
            })
    }, []);

    useEffect(() => {
        axiosClient.get('/top-category')
            .then((res) => {
                setCategory(res.data)
            })
    }, []);

    useEffect(() => {
        axiosClient.get('/top-categories')
            .then((res) => {
                setTopCategories(res.data)
            })
    }, []);

    useEffect(() => {
        axiosClient.get('/popular-tags')
            .then((res) => {
                setPopularTags(res.data)
            })
    }, []);

    useEffect(() => {
        axiosClient.get('/feature-post')
            .then((res) => {
                setFeaturePost(res.data)
            })
    }, []);

    const onSubmit = (e) => {
        e.preventDefault()
        const payload = {
            query: searchRef.current.value
        }
        navigate('/search/'+payload.query)        
    }
    
    return(
        <>
            {isLoading ? <Loader /> : 
            <>
            <div>
                <div className='max-w-screen-xl mx-auto px-4'>
                    <div className='flex flex-row'>
                        <div className='basis-5/12 border-t border-gray-500 w-full mt-4'></div>
                        <div className='basis-2/12 w-5/12 text-center text-lg dark:text-white'>
                            Top Categories
                        </div>
                        <div className='basis-5/12 border-t border-gray-500 w-full mt-4'></div>
                    </div>

                    <div className='flex flex-row gap-5 my-5'>
                        {category &&
                            category.map((item) => {
                                return (
                                    <Link to={`/category/${item.slug}`} className={`basis-1/5 h-32 w-full text-white text-center pt-12 text-lg ${item.category_color}`}>
                                        <p>{item.name}</p>
                                    </Link>
                                )                                           
                            })
                        }                                                               
                    </div>
                </div>
            </div>

            <div className='my-5'>
                <div className="max-w-screen-xl mx-auto px-4">

                    <div className='flex flex-row'>
                        <div className='basis-5/12 border-t border-gray-500 w-full mt-4'></div>
                        <div className='basis-2/12 w-5/12 text-center text-lg dark:text-white'>
                            All Categories
                        </div>
                        <div className='basis-5/12 border-t border-gray-500 w-full mt-4'></div>
                    </div>
                    
                    <div className="flex flex-row gap-5 mb-5 mt-5">
                        <div className="basis-3/4">
                            
                            {currentItems && 
                                currentItems.map((item, index) => {
                                    return(
                                        <div className="flex flex-row mb-5" key={index}>
                                            <div className='flex-none w-64 h-64'>
                                                <Link to={`/post/${item.slug}`}>
                                                    <img className='h-64 w-64' src={item.title_image} alt="Post"/>   
                                                </Link>
                                            </div>
                                            <div className='h-64 p-5 bg-gray-200 dark:bg-gray-700 text-black dark:text-white grow'>
                                                <Link to={`/post/${item.slug}`}><h1 className='text-xl mt-2 mb-5'>{item.title.substring(0, 65)}</h1></Link>
                                                <p className='mb-5'>{item.description.substring(0, 330)}</p>
                                                <Link to={`/post/${item.slug}`} className='bg-black text-white dark:bg-gray-200 dark:text-black p-2'>Details</Link>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                            
                            {post && 
                                <BlogPostPaginate data={post} setCurrentItems={setCurrentItems}/>
                            }

                        </div>                        
                        <div className="basis-1/4">
                            <div className='bg-gray-200 dark:bg-gray-700 text-black dark:text-white mb-5'>
                                <h1 className='text-center p-3 text-xl'>Search</h1>
                                <div className='px-5 pb-[39px]'>
                                    <form onSubmit={onSubmit}>
                                        <div className="relative md:block">
                                            <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                                                <svg className="w-4 h-4 text-gray-500 dark:text-gray-400" aria-hidden="true" fill="none" viewBox="0 0 20 20">
                                                <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m19 19-4-4m0-7A7 7 0 1 1 1 8a7 7 0 0 1 14 0Z"/>
                                                </svg>
                                                <span className="sr-only">Search icon</span>
                                            </div>
                                            <input type="search" ref={searchRef} id="search-navbar" className="block w-full p-2 pl-10 text-sm text-gray-900 border border-gray-300 rounded-lg bg-gray-50 focus:ring-gray-500 focus:border-gray-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-500 dark:focus:border-gray-500" placeholder="Search..." required/>
                                            <button type="submit" className="hidden">Search</button>
                                        </div>
                                    </form>
                                </div>
                            </div>

                            <div className='bg-gray-200 dark:bg-gray-700 text-black dark:text-white mb-5'>
                                <h1 className='text-center p-3 text-xl'>Top Categories</h1>
                                <div className='pl-5 pr-5 pb-7'>
                                    <ul className="w-full text-sm font-medium text-gray-900 bg-white border border-gray-200 rounded-lg dark:bg-gray-700 dark:border-gray-600 dark:text-white max-h-[372px] overflow-y-auto">
                                    {topCategories && 
                                            topCategories.map((item, index) => {
                                                return(
                                                    <li className="w-full px-4 py-2 border-b border-gray-200 rounded-t-lg dark:border-gray-600">
                                                        <Link to={`/category/${item.slug}`}>{index+1}. {item.name.substring(0, 31)}</Link>
                                                    </li>
                                                )
                                            })
                                        }                                       
                                    </ul>
                                </div>
                            </div>

                            <div className='bg-gray-200 dark:bg-gray-700 text-black dark:text-white mb-5'>
                                <h1 className='text-center p-3 text-xl'>Popular Tags</h1>
                                <div className='pl-5 pr-5 pb-7'>
                                    <ul className="w-full text-sm font-medium text-gray-900 bg-white border border-gray-200 rounded-lg dark:bg-gray-700 dark:border-gray-600 dark:text-white max-h-[372px] overflow-y-auto">
                                    {popularTags && 
                                            popularTags.map((item, index) => {
                                               return(
                                                    <li className="w-full px-4 py-2 border-b border-gray-200 rounded-t-lg dark:border-gray-600">
                                                        <Link to={`/tag/${item.tag}`}>{index+1}. {item.tag.substring(0, 31)}</Link>
                                                    </li>

                                               ) 
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>

                            <div className='bg-gray-200 dark:bg-gray-700 text-black dark:text-white mb-5'>
                                <h1 className='text-center p-3 text-xl'>Feature Post</h1>
                                <div className='pl-5 pr-5 pb-7'>
                                    <ul className="w-full text-sm font-medium text-gray-900 bg-white border border-gray-200 rounded-lg dark:bg-gray-700 dark:border-gray-600 dark:text-white overflow-y-auto">
                                        {featurePost && 
                                            featurePost.map((item, index) => {
                                               return(
                                                    <li className="w-full px-4 py-2 border-b border-gray-200 rounded-t-lg dark:border-gray-600">
                                                        <Link to={`/post/${item.slug}`}>{index+1}. {item.title.substring(0, 31)}</Link>
                                                    </li>

                                               ) 
                                            })
                                        }
                                        
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                   
                </div>
            </div>
            </>
            }
        </>
    )
}