import { useEffect, useRef, useState } from "react";
import axiosClient from "../axios-client";
import Loader from "../components/Loader";

export default function Contact() {

  const [msg, setMsg] = useState()
  const [errors, setErrors] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  
  const nameRef = useRef()
  const emailRef = useRef()
  const phoneRef = useRef()
  const messageRef = useRef()
  
  useEffect(() => {
    document.title = 'Contact - React Blog';
    setTimeout(() => {
      setIsLoading(false);
    }, 500);
  }, []);

  const onSubmit = (e) => {
    e.preventDefault()
    const payload = {
        name: nameRef.current.value,
        email: emailRef.current.value,
        phone: phoneRef.current.value,
        message:messageRef.current.value
    }
    axiosClient.post('/send-message', payload)
    .then(({data}) => {
        setMsg(data.message)
        setTimeout(()=> {
            setMsg(null)
        },3000)
      
        nameRef.current.value = "";
        emailRef.current.value = "";
        phoneRef.current.value = "";
        messageRef.current.value = "";

    })
    .catch(err => {
        const response = err.response;
        if (response && response.status === 422) {
            setErrors(response.data.errors)
        }
    })
}
  
  return (
    <>
      {isLoading ? <Loader /> : 
        <div className='max-w-screen-xl mx-auto px-4 mt-3'>
            <div className='bg-gray-200 dark:bg-gray-700 min-h-[540px] rounded-xl'>
                <div className='text-center text-2xl dark:text-white pt-3'>
                    Contact
                </div>
                <div className='min-h-[520px] flex flex-row'>
                  <div className="basis-1/2">
                    <img src="/img/contact-us.png" alt="contact" className="h-96 block m-auto mt-16"/>
                  </div>             
                  <div className="basis-1/2">
                    <div className="h-[440px] bg-gray-100 dark:bg-gray-900 mt-7 mr-14 rounded-xl p-7">
                      <form onSubmit={onSubmit}>
                        {errors &&
                            <div className="alert mb-3">
                            {Object.keys(errors).map(key => (
                                <p key={key}>{errors[key][0]}</p>
                            ))}
                            </div>
                        }
                        <div className="relative z-0 w-full mb-6 group">
                            <input type="text" name="floating_name" id="floating_name" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " ref={nameRef} required />
                            <label htmlFor="floating_name" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Your name</label>
                        </div>
                        <div className="relative z-0 w-full mb-6 group">
                            <input type="email" name="floating_email" id="floating_email" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " ref={emailRef} required />
                            <label htmlFor="floating_email" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Email address</label>
                        </div>
                        <div className="relative z-0 w-full mb-6 group">
                              <input type="tel" pattern="[0-9]{5}[0-9]{6}" name="floating_phone" id="floating_phone" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " ref={phoneRef} required />
                              <label htmlFor="floating_phone" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Phone number (0123-123456)</label>
                        </div>
                        <div className="relative z-0 w-full mb-6 group">
                            <textarea type="text" name="repeat_message" id="floating_repeat_message" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " rows="5" ref={messageRef} required ></textarea> 
                            <label htmlFor="floating_repeat_message" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Message</label>
                        </div>
                        <div className="flex">
                          <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Send</button>
                          <p className='pt-2 pl-5 text-white'>{msg}</p>
                        </div>
                      </form>
                    </div>
                  </div>             
                </div>
            </div>
        </div>
      }
    </>
  )
}
